@import url(https://unpkg.com/bootstrap@4.1.0/dist/css/bootstrap.min.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*css needs to be fixed with adjustment of frame size*/

.wallnav {
    /*overflow:hidden;*/
    background-color: #bcbcd3;
    display: inline-block;
    width: 100%;
    color: white;
    -webkit-align-items:center;
            align-items:center;
    height:10%;
}

.wallLogo {
    font: 40px courier,serif;
    font-weight: bold;
    margin-top: 10px;
    margin-left: 5%;
    margin-right: 5%;
    width: 300px;
    vertical-align: baseline;
    display: inline-block;
    color:black;
}

.Greetings{
    font: 20px courier,serif;
    font-weight: bold;
    text-align: right;
    display: inline;
    float: right;
    color:black;
}

.profilePic{
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 5px;
    width: 100px;
    border-radius: 8px;
    padding:5px;
    vertical-align: middle;
    width: 150px;
    margin-left:10%;
    margin-right: 10%;
    margin-top:10px;
    margin-bottom:3px;
}

.search{
    text-align: center;

    display: inline-block;
    width: 500px;
}

.search input {
  width: 300px;
  height: 30px;
}

.search button {
  height: 30px;
  vertical-align: middle;
  padding-top: 7px;
  padding-bottom: 23px;
}

.search ul {
  float: left;
  position:absolute;
  background-color: white;
  margin-left: 40px;
  border: 1px solid black;
  width: 300px;
  color: black;
  list-style-type: none;
}

.search ul li {
  text-align: left;
  text-decoration: none;
}


.sidenav,.activityNavBar {
    height:100%;
    width:15%;
    background-color: rgb(235, 216, 193);
   
}

.sidenav a, .activityNavBar a{
    display:block;
    padding: 8px 8px 8px 32px;
    margin-bottom: 20px;
    font-size: 25px;
    color:#4c4444;
    text-decoration: none;
}

.sidenav a:hover{
    color:#f1f1f1;
}

.activityNavBar{
    float:right;
}

.sidenav{
    float:left;
}
.postContainer{
    border: 2px solid gray;
    border-radius: 5px;
    width: 90%;
    display: inline-block;
    margin:3%;
    padding:5px;
    text-align: justify;
}

.userImage, .userName, .editButton{
    display: inline-block;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 40px;
  background-color: #222222;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


